.gform {
  width: 100%;
}

.form-elements {
  position: relative;
}

.form-personal {
  display: flex;
  grid-gap: 2em;
  gap: 2em;
}

.form-personal .pure-group:first-child {
  width: 70%;
}
.form-personal .pure-group:last-child {
  width: 100%;
}

.form-value {
  font-size: 1.4rem;
  padding: 0.5rem 0;
  padding-left: 1rem;
  margin-bottom: 0.5rem;

  border: none;
  border-bottom: 2px solid #000;
  background: transparent;
  outline: none;

  color: #fff;
}

#name {
  width: 100%;
}

#email {
  width: 100%;
}

#message {
  width: 100%;
}

.form-value:focus {
  border-bottom: 2px solid #03e9f4;
}

.btn-submit {
  position: absolute;
  right: 0;
  cursor: pointer;

  padding: 0.5rem 2rem;
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  border-radius: 0.5rem;
  background: transparent;
}

.thankyou_message {
  color: #03e9f4;
  font-size: 2rem;
}

/* Animation */
.btn-submit:hover,
.btn-submit:active {
  -webkit-animation: effectBg 1s forwards;
          animation: effectBg 1s forwards;
}

@-webkit-keyframes effectBg {
  100% {
    background: #03e9f4;
    box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
      0 0 100px #03e9f4;
  }
}

@keyframes effectBg {
  100% {
    background: #03e9f4;
    box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
      0 0 100px #03e9f4;
  }
}

